<template>
  <v-app>
    <v-toolbar fixed app :clipped-left="clipped">
      <v-toolbar-side-icon @click.native.stop="drawer = !drawer"></v-toolbar-side-icon>
      <v-toolbar-title v-text="title"></v-toolbar-title>
      <v-spacer></v-spacer>

<!-- FOR TOOLBAR ITEMS SPACING: create a container with fluid class and row wrap -->
      <v-toolbar-items>

<!-- Projects Button & Functionality -->
          <v-menu offset-y>
            <v-btn flat light slot="activator">Projects</v-btn>
            <v-list>
              <v-list-tile v-for="project in projects" :key="project.title" @click="">
                <v-list-tile-title>{{ project.title }}</v-list-tile-title>
              </v-list-tile>
            </v-list>
          </v-menu>

<!-- Blog Button & Functionality-->
        <v-btn flat>Blog</v-btn>

<!-- Search Button & Functionality -->
        <v-btn fab light icon>
          <v-icon>search</v-icon>
        </v-btn>

      </v-toolbar-items>

<!-- User Menu -->
      <v-menu
              offset-y
              :close-on-content-click="false"
              :nudge-width="200"
              v-model="menu"
      >
        <v-avatar size="36px" slot="activator">
          <img src="@/assets/jbavatar.png" alt="JB">
        </v-avatar>
        <v-card>
          <v-list>
            <v-list-tile avatar>
              <v-list-tile-avatar>
                <img src="@/assets/jbavatar.png" alt="Jeanette">
              </v-list-tile-avatar>
              <v-list-tile-content>
                <v-list-tile-title>{{ user }}</v-list-tile-title>
                <v-list-tile-sub-title>{{ userEmail }}</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <v-divider></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="primary" flat @click="menu = false">My Account</v-btn>
          </v-card-actions>
        </v-card>
      </v-menu>

    </v-toolbar>

      <v-content>
<!-- Jumbotron -->
    <v-jumbotron color="grey lighten-2">
      <v-container fill-height>
        <v-layout align-center>
          <v-flex>
            <h3 class="display-3">Welcome to the site</h3>
            <span class="subheading">Lorem ipsum dolor sit amet, pri veniam forensibus id. Vis maluisset molestiae id, ad semper lobortis cum. At impetus detraxit incorrupte usu, repudiare assueverit ex eum, ne nam essent vocent admodum.</span>
            <v-divider class="my-3"></v-divider>
            <div class="title mb-3">Check out our newest features!</div>
            <v-btn large color="primary" class="mx-0">See more</v-btn>
          </v-flex>
        </v-layout>
      </v-container>
    </v-jumbotron>


      <v-container fluid grid-list-md>

<!-- Card -->
          <v-layout align-center justify-center row wrap >
              <v-flex d-flex xs12 sm6 md3 v-for="project in projects" :key="project.title">
                  <v-card >
                      <v-card-media :src="project.src" height="200px">
                      </v-card-media>
                      <v-card-title primary-title>
                          <div>
                              <div class="headline">{{ project.title }}</div>
                              <span class="grey--text">{{ project.description }}</span>
                          </div>
                      </v-card-title>
                      <v-card-actions>
                          <v-btn flat color="red" @click.native="show = !show">
                              {{ show ? 'More Info':'More Info' }}
                          </v-btn>
                          <v-spacer></v-spacer>
                      </v-card-actions>
                      <v-slide-y-transition>
                          <v-card-text v-show="show">
                              {{ project.moreInfo }}
                          </v-card-text>
                      </v-slide-y-transition>
                  </v-card>
              </v-flex>
          </v-layout>


      </v-container>
      <p>Repositories grabbed: {{ repositoriesCount }}</p>
  </v-content>

      <v-footer class="pa-3">
          <v-spacer></v-spacer>
          <div>&copy; {{ new Date().getFullYear() }}</div>
      </v-footer>
    </v-app>
</template>

<script>
export default {
  data () {
    return {
      clipped: true,
      fixed: false,
      projects: [
      { title: 'Project 1', description: 'This is project 1', moreInfo: 'This is more info for project 1', src: 'https://loremflickr.com/320/240/rio' },
      { title: 'Project 2', description: 'This is project 2', moreInfo: 'This is more info for project 2', src: 'https://loremflickr.com/320/240/dog' },
      { title: 'Project 3', description: 'This is project 3', moreInfo: 'This is more info for project 3', src: 'https://loremflickr.com/320/240/brazil,rio' },
      { title: 'Project 4', description: 'This is project 4', moreInfo: 'This is more info for project 4', src: 'https://loremflickr.com/320/240/paris' }
      ],
      show: false,
      miniVariant: false,
      title: 'Devport.io',
      menu: false,
      user: 'Jeanette Bridges',
      userEmail: 'jeanettebridges@gmail.com',
      myGitHubData: {}
    }
  },
  mounted: function () {
    this.$http({
      url: 'https://api.github.com/users/markbridges/repos',
      method: 'GET'
    }).then(function (response) {
      this.myGitHubData = response.data
    }, function (response) {
      console.log(response.data)
    })
  },
  computed: {
    repositoriesCount: function () {
      if (this.myGitHubData) {
        return this.myGitHubData.length
      }
      return 'none yet...'
    }
  },
  name: 'App'
}
</script>



